.right-popup {
  position: fixed;
  right: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: #000;
    opacity: 0;
    visibility: hidden;
  }
  &__container {
    position: fixed;
    padding: 14px 25px;
    background: #fff;
    height: 100vh;
    width: 50dvw;
    right: 0;
    top: 0;
    //   max-height: 639px;
    box-shadow: 0px 4px 12.8px rgba(0, 0, 0, 0.25);
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(50%);
  }
  &.open {
    visibility: visible;
    opacity: 1;
  }
  &.open::before {
    opacity: 0.25;
    visibility: visible;
  }
  &.open &__container {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
  }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 35px;
  }
  &__title {
    font-size: 24px;
  }
  &__close {
    display: flex;
    max-width: 24px;
    max-height: 24px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__main {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__btn {
    background: #000;
    color: #fff;
    font-size: 16px;
    font-weight: 300;
    width: 240px;
    height: 50px;
    border: 1px solid #000;
    border-radius: 15px;
    transition: all 0.2s;
  }
  &__btn:hover {
    background: #fff;
    color: #000;
  }
}

.settings-popup {
  visibility: hidden;
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__item {
  }
  &__item-link {
    max-width: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 12px 30px;
    width: 100%;
    height: 100%;
    color: #000;
    font-size: 16px;
    font-weight: 300;
    border: 1px solid #000;
    border-radius: 15px;
    transition: all 0.2s;
  }
  &__item-link:hover {
    border-top-right-radius: 0;
  }
}

.filter-popup {
  &__list {
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 5px;
    max-width: 240px;
    span {
      font-size: 16px;
      font-weight: 400;
    }

    label {
      display: flex;
      width: 100%;
    }
  }
  &__input {
  }
  &__btn {
  }
  &__select {
    width: 100%;
    min-height: 44px;
  }

  &__label-input {
    width: 100%;
    border: 1px solid #000;
    min-height: 44px;
    border-radius: 5px;
    overflow: hidden;
    input {
      width: 100%;
      padding: 0 10px;
      font-size: 18px;
      color: #000;
      border-radius: 5px;
    }
  }
}

.select2-selection {
  width: 100%;
  display: flex !important;
  align-items: center;
  justify-content: space-between;
  min-height: 44px !important;
  border-color: #000 !important;
  &__rendered {
    color: #000 !important;
  }
}

.select2-selection__arrow {
  display: flex;
  position: relative !important;
  top: 0 !important;
  right: 0 !important;
}
