.add-popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: #000;
    visibility: hidden;
    opacity: 0;
  }
  &__container {
    padding: 20px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    width: 450px;
    height: 460px;
    border-radius: 15px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s;
    top: 0;
    transform: translate(-50%, 0);
  }
  &.open {
    visibility: visible;
    opacity: 1;
  }
  &.open::before {
    visibility: visible;
    opacity: 0.25;
  }
  &.open &__container {
    visibility: visible;
    opacity: 1;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
  }
  &__title {
    font-size: 20px;
  }
  &__close {
    max-width: 24px;
    max-height: 24px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__main {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }
  &__btn {
    background: #000;
    color: #fff;
    font-size: 16px;
    font-weight: 300;
    width: 156px;
    height: 50px;
    border: 1px solid #000;
    border-radius: 15px;
    transition: all 0.2s;
  }
  &__btn:hover {
    background: #fff;
    color: #000;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 240px;
    gap: 5px;
    span {
      font-size: 16px;
    }
  }
  &__label-input {
    border: 1px solid #000;
    width: 100%;
    height: 44px;
    min-height: 44px;
    overflow: hidden;
    border-radius: 5px;
    input {
        width: 100%;
      height: 100%;
      font-size: 18px;
      border-radius: 5px;
      padding: 0 10px;
    }
  }
  &__select {
    width: 100%;
  }
}
