.table {
  max-width: 1585px;
  max-height: 639px;
  &__container {
    padding: 10px 28px;
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 35px;
    border-bottom: 1px solid #000;
    padding-bottom: 15px;
  }
  &__head-list {
    display: flex;
    align-items: center;
    gap: 90px;
  }
  &__head-item {
    font-size: 20px;
    min-width: 180px;
    text-align: center;
    cursor: pointer;
  }
  &__head-search {
    display: flex;
    align-items: center;
    gap: 13px;
  }
  &__head-label {
    display: flex;
    gap: 5px;
    width: 220px;
    height: 30px;
    border: 1px solid #000;
    border-radius: 15px;
    padding: 0px 12px;
    overflow: hidden;
    img {
      max-width: 14px;
      max-width: 14px;
    }

    input {
      padding-right: 4px;
      font-size: 16px;
      max-width: 140px;
      width: 100%;
      height: 100%;
      border-radius: 15px;
    }
  }
  &__head-select {
    width: 190px;
    height: 30px;
  }
  &__head-btn {
    display: flex;
    width: 100%;
    max-width: 24px;
    img {
    }
  }

  &__head-settings {
  }
  &__main {
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 15px;
  }
  &__row-main {
    display: flex;
    align-items: center;
    padding-left: 35px;
    gap: 90px;
  }

  &__row-item {
    max-width: 180px;
    min-width: 180px;
    font-size: 20px;
    text-align: center;
  }
  &__row-btns {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__row-btn {
    
    height: 24px;

    img {
        height: 100%;
    }
  }
}
